import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { WelcomeComponent } from './welcome/welcome.component';
import { ErrorComponent } from './error/error.component';
import { UsererrorComponent } from './usererror/usererror.component';
import { PasserrorComponent } from './passerror/passerror.component';
import { TodoslistComponent } from './todoslist/todoslist.component';
import { LogoutComponent } from './logout/logout.component';
import { RouteGuardService } from './services/route-guard.service';

const routes: Routes = [
  {path: '', component: LoginComponent},
  {path: 'welcome/:name', component: WelcomeComponent,  canActivate: [RouteGuardService]}, 
  {path: 'login', component: LoginComponent},
  {path: 'usererror/:name', component: UsererrorComponent},
  {path: 'passerror/:name', component: PasserrorComponent},
  {path: 'todoslist', component: TodoslistComponent,  canActivate: [RouteGuardService]},
  { path: 'logout', component: LogoutComponent},

  {path: '**', component: ErrorComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
